<template>
  <div>
    <div class="ass1-content-head">
		</div>
    <h3 v-if="postList.length == 0" class="result-query">Không tìm thấy dữ liệu nào phù hợp với '{{query}}'</h3>
    <h3 v-if="postList.length != 0" class="result-query"><span>({{postList.length}})</span> Kết quả: Cho '{{query}}'</h3>
    <posts-list v-bind:postList="postList"/>
  </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import PostsList from '@/components/homepage/PostsList'

export default {
  name: 'search-list',
  components: {
    PostsList
  },  
  data () {
    return {
      query: this.$route.params.query,
      postList: []
      // postList: [{"USERID":"3","fullname":"Huỳnh Thị Minh Trúc","profilepicture":"http://localhost:80/assignment2-ltw-php/public/user/3/32294391_163827007620829_994025092092526592_n.jpg","url_image":"https://i.memeful.com/media/post/ER5rbyd_700wa_0.gif","PID":"26","post_content":"Chúng mày có thấy thấm không\n","time_added":"2018-12-06 09:38:16.152877","status":"1"},{"USERID":"3","fullname":"Huỳnh Thị Minh Trúc","profilepicture":"http://localhost:80/assignment2-ltw-php/public/user/3/32294391_163827007620829_994025092092526592_n.jpg","url_image":"https://i-xem.mkocdn.com/i.xem.sb/data/photo/2018/07/02/005/khi-ban-hoc-kien-truc-nhung-den-bai-thiet-ke-cau-thang-ban-lai-bo-hoc-1530523055-650.jpg","PID":"27","post_content":"Khi bạn học kiến trúc nhưng đến bài thiết kế cầu thang bạn lại bỏ học.\n","time_added":"2018-12-06 09:41:27.247307","status":"1"}]
    }
  },
  watch: {
    '$route' (to, from) {
      this.query = this.$route.params.query;
      this.fetchDataSearch();
    }
  },
  created() {
    this.fetchDataSearch();
  },
  methods: {
    async fetchDataSearch() {
      AlertService.loadingPopup();
      this.postList = [];
      const res = await this.$store.dispatch('searchPost', this.query);
      // console.log(res);
      if(res.ok) {
        this.postList = res.body;
      }
      AlertService.swal.close();
    }
  }
}
</script>

<style scoped>
  .result-query {
    margin-bottom: 30px;
  }
  .result-query span {
    font-weight: 700;
  }
</style>



