<template>
    <div class="ass1-login">
        <div class="ass1-login__content">
            <p>Profile</p>

            <div class="ass1-login__form" v-if="cloneUser">
                <div class="avatar">
                    <img :src="reviewAvatar.base64 || cloneUser.profilepicture || 'static/images/default-avatar.png'" alt="">
                </div>
                <form>
                    <input v-model="cloneUser.fullname" type="text" class="form-control" placeholder="Tên ..." required="">
                    <!-- <input type="text" class="form-control" placeholder="Mật khẩu mới" required=""> -->
                    <!-- <input type="text" class="form-control" placeholder="Xác nhận mật khẩu mới" required=""> -->
                    <select v-model="cloneUser.gender" class="form-control">
                        <option value>Giới tính</option>
                        <option value="nam">Nam</option>
                        <option value="nu">Nữ</option>
                    </select>
                    <input 
                        type="file" accept="image/*" @change="onImageChange"
                        name="avatar"  
                        placeholder="Ảnh đại diện" 
                        class="form-control">
                    <textarea v-model="cloneUser.description" class="form-control" cols="30" rows="5" placeholder="Mô tả ngắn ..."></textarea>
                    <div class="ass1-login__send justify-content-center">
                        <!-- <a href="dang-ky.html">Đăng ký một tài khoản</a> -->
                        <button @click="(e) => handleUpdateInfo(e)" class="ass1-btn">Cập nhật</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import { mapGetters } from 'vuex';
import _ from 'lodash';

export default {
    name: 'edit-profile',
    data() {
        return {
            cloneUser: null,
            reviewAvatar: {
                base64: null,
                objFile: null
            },
        }
    },
    methods: {
        onImageChange(e) {
            const files = e.target.files;
            if (!files.length) {
                if(!this.reviewAvatar.objFile) {
                    AlertService.warningPopup('Cảnh báo', 'Chưa chọn file!');
                }
                return
            }
            this.createImage(files[0]);
        },
        createImage(file) {
            var reader = new FileReader();
            var vm = this;
            var type = file.type;
            // let arrImg = ['jpeg','jpg','png'];
            if( type.indexOf('png') != -1 || 
                type.indexOf('jpeg') != -1 || 
                type.indexOf('jpg') != -1) {
                    reader.onload = (e) => {
                        vm.reviewAvatar.base64 = e.target.result;
                        vm.reviewAvatar.objFile = file;
                        // vm.reviewAvatar.objFile = file;
                    };
                    reader.readAsDataURL(file);
            } else {
                AlertService.warningPopup('Cảnh báo', 'Hình ảnh không đúng định dạng!');
            }
        },
        handleUpdateInfo(e) {
            e.preventDefault();
            AlertService.loadingPopup();
            this.$store.dispatch('updateInfoUser', { user: this.cloneUser, objImage: this.reviewAvatar.objFile }).then((res) => {
                // console.log("edit profile", res)
                AlertService.swal.close();
                if(res.ok) {
                    AlertService.successPopup('Thành công!', 'Cập nhật thông tin thành công');
                } else {
                    AlertService.errorPopup('Thất bại!', res.error)
                }
            });
        }
    },
    created() {
        this.cloneUser = _.clone(this.currentUser);
    },
    computed: {
         ...mapGetters([
            'currentUser'
        ])
    }
}
</script>

<style>

</style>



