<template>
    <div class="ass1-login">
        <div class="ass1-login__logo">
            <router-link to="/" class="ass1-logo">Nhóm XX</router-link>
        </div>
        <div class="ass1-login__content">
            <p>Đăng ký một tài khoản</p>
            <div class="ass1-login__form">
                <form>
                    <div v-if="error.length" class="alert alert-danger" role="alert">{{error.join('.')}}</div>
                    <input v-model="data.fullname" type="text" class="form-control" placeholder="Tên hiển thị" required="">
                    <input v-model="data.email" type="email" class="form-control" placeholder="Email" required="">
                    <input v-model="data.password" type="password" class="form-control" placeholder="Mật khẩu" required="">
                    <input v-model="data.repassword" type="password" class="form-control" placeholder="Nhập lại mật khẩu" required="">
                    <div class="ass1-login__send">
                        <router-link to="/login">Đăng nhập</router-link>
                        <button v-on:click="(e) => handleRegister(e)" class="ass1-btn">Đăng ký</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
export default {
    name: 'register',
    data() {
        return {
            data: {
                fullname: '',
                email: '',
                password: '',
                repassword: ''
            },
            error: []
        }
    },
    created() {
        // console.log(this.$router);
    },
    methods: {
        verify() {
            let error = [];
            if(this.data.password != this.data.repassword) {
                error.push('Mật khẩu không khớp!');
            }
            if(!this.data.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i)) {
                error.push('Email không hợp lệ!')
            }
            this.error = error;
        },
        async handleRegister(e) {
            e.preventDefault();
            AlertService.loadingPopup();
            this.verify();
            if(!this.error.length) {
                const res = await this.$store.dispatch('register', this.data);
                AlertService.swal.close();
                if(res.ok) {
                    AlertService
                    .successPopup(
                        'Success!', 
                        'Đăng kí thành công, quay lại trang chủ',
                        (e) => {
                        this.$router.push({ path: '/' })
                    });
                } else {
                    AlertService.errorPopup('Error!', res.error.message);
                }
            } else {
                AlertService.swal.close();
            }
        }
    }
}
</script>

<style>

</style>



