<template>
    <div>
        <!--content head-->
        <div class="ass1-content-head ass1-content-head__flex-off">
            <div class="row">
                <div class="col-lg-8">
                </div>
                <div class="col-lg-4">
                    <div class="ass1-content-head__right-col">
                        <div class="ass1-content-head__text">
                            <div>Bài Post gần đây</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--sections-->
        <div class="row">
            <div class="col-lg-8">
                <!--section-->
                <div v-if="postList.length != 0">
                    <posts-list 
                        v-bind:styleComp="false"
                        v-bind:postList="postList"/>
                    <button 
                        v-on:click="loadNextPage"
                        :class="classLoader"
                        class="load-more ass1-btn">
                        <span>Xem thêm</span>
                        <img v-show="loading" src="/static/loader.gif" alt="loader">
                    </button>
                </div>
                <div v-if="postList.length == 0">
                    <div class="ass1-content-head__text">
                        <div>Không tìm thấy bài viết nào thuộc danh mục này
                            <router-link to="/">Quay lại trang chủ</router-link>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4">
               <posts-list 
                    v-bind:styleComp="false"
                    v-bind:postList="recentPost"/>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import PostsList from '@/components/homepage/PostsList'
import { mapGetters } from 'vuex';
export default {
    name: 'categories',
    components: {
        PostsList
    },
    data() {
        return {
            tagIndex: this.$route.params.id,
            author: null,
            recentPost: [],
            postList: [],
            currentPage: 0,
            pageSize: 3,
            loading: false,
            init: true,
        }
    },
    watch: {
        '$route' (to, from) {
            this.tagIndex = to.params.id;
            this.fetchRecentPost();
        }
    },
    created() {
        this.fetchPostByCategory();
        this.fetchRecentPost();
    },
    computed: {
        ...mapGetters([
            'isAuthenticated'
        ]),
        dateFormat: function() {
            if(this.dataPost) {
                var d = new Date(this.dataPost.post.time_added)
                d = new Date(d).getTime();
                return this.setTimeAgo(d);
            }
            return null;
        },
        classLoader() {
            return {
                'loading': this.loading
            }
        }
    },
    methods: {
        loadNextPage() {
            this.fetchPostByCategory();
        },
        async fetchPostByCategory() {
            if(this.init) { AlertService.loadingPopup() }
            this.loading = true;
            this.currentPage = this.currentPage + 1;
            const data = {
                currentPage: this.currentPage,
                pageSize: this.pageSize,
                tagIndex: this.tagIndex
            };
            const res = await this.$store.dispatch('getListPostByCategories', data);
            // console.log(res);
            if(res.ok) {
                // this.postList.push( res.body );
                const newArray = [...this.postList, ...res.body];
                this.postList = newArray;
                this.loading = false;
            } else { this.loading = false; }
            if(this.init) {
                this.init = false;
                AlertService.swal.close();
            }
        },
        async fetchRecentPost() {
            AlertService.loadingPopup();
            const data = {
                currentPage: 1,
                pageSize: 3
            };
            this.recentPost=[]
            const res = await this.$store.dispatch('getListPostPagination', data);
            if(res.ok) {
                this.recentPost = res.body;
            }
            AlertService.swal.close();
        },
        setTimeAgo(_date){
            var vm = this;
            var _time = new Date();
            _time = new Date(_time);
            vm.timeAgo = _time.getTime();
            var sub = (vm.timeAgo - _date)/1000;
            var re_val = "";
            if (sub >= 0 && sub < 1){
                return "Vừa xong";
            }
            else if (sub <= 60){
                var temp_str = " giây ";
                re_val = Math.floor(sub).toString() + temp_str + "trước";
            }
            else if (sub <= 3600){
                var temp_str = " phút ";
                re_val = Math.floor(sub/60).toString() + temp_str + "trước";
            }
            else if (sub <= 86400){
                var temp_str = " giờ ";
                re_val = Math.floor(sub/3600).toString() + temp_str + "trước";
            }
            else {
                var temp_str = " ngày ";
                re_val = Math.floor(sub/86400).toString() + temp_str + "trước";
            }
            return re_val;
        }
    }
}
</script>

<style scoped>
    .ass1-section__avatar.ass1-avatar {
        position: relative;
        border-radius: 10%;
        overflow: hidden;
    }
    .ass1-section__avatar.ass1-avatar img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .ass1-section__feeling li {
        display: inline-block;
        margin-right: 10px;
        margin-top: 10px;
    }
    .ass1-section__feeling li a {
        display: block;
        font-size: 13px;
        padding: 5px 15px;
        border-radius: 15px;
        border: 1px solid rgba(204, 204, 204, 0.5);
        background-color: #fff;
        box-shadow: 0 0 0 0 rgba(0,0,0,0.1);
        transition: all .3s ease;
    }
    .ass1-section__feeling li a:hover {
        border-color: rgba(204, 204, 204, 0.3);
        box-shadow: 1px 1px 20px 0px rgba(0,0,0,0.1);
    }
    .load-more span {
        transform: scale(1);
        transition: all .3s ease;
    }
    .load-more img {
        position: absolute;
        max-height: 45px;
        top: 50%;
        left: 50%;
        width: auto;
        transform: translate(-50%, -50%);
        pointer-events: none;
        visibility: hidden;
        opacity: 0;
    }
    .load-more.loading img {
        opacity: 1;
        visibility: visible;
    }
    .load-more.loading span {
        transform: scale(0);
    }
    .load-more.ass1-btn:hover {
        color: #ffffff;
        background-color: #3482e2;
    }
    :focus {
        outline: none;
    }
    .load-more {
        min-width: 150px;
        margin: 0 auto;
        justify-content: center;
        margin-top: 50px;
        margin-bottom: 100px;
        position: relative;
    }
</style>


