										<li><a href="/">Sport</a></li>
									</ul> -->
								</div>
								<div class="ass1-header__menu-transition"></div>
							</div>
						</li>
						<li class="active">
							<a href="/">Hot</a>
							<div class="ass1-header__nav" style="display: none;">
								<div class="container">
									<ul>
										<li><a href="/">Funny</a></li>
										<li><a href="/">Animals</a></li>
