<script>
import AlertService from '@/plugins/AlertService.js'
import { mapGetters } from 'vuex'
export default {
    name: 'app-header',
    data() {
        return {
            query: '',
        }
	},
	computed: {
		...mapGetters([
			'allCategories'
		]),
		current_user() {
			return this.$store.getters.currentUser;
		}
	},
	mounted() {
		setTimeout(() => {
			$(".ass1-header__menu li > a").click(function(e) {
				// $(".ass1-header__nav").hide();
				$(this).parent().find(".ass1-header__nav").slideToggle(300, 'swing');
			});
		}, 500);
	},
	methods: {
		handleLogout() {
			AlertService.warningPopup('Cảnh báo!', 'Bạn có thực sự muốn đăng xuất?', (e) => {
				if(e.value) {
					this.$store.dispatch('logout');
					this.$router.push('/');
				}
			});
		},
		handleSubmit(e) {
			e.preventDefault();
			if(this.query != '') {
				this.$router.push({
					name: 'search-post',
					params: {
						query: this.query
					}
				})
			}
			// console.log(e);
		},
		stringToURL(str) {
			var VietnameseSigns = [ "aAeEoOuUiIdDyY-",
				"áàạảãâấầậẩẫăắằặẳẵ",
				"ÁÀẠẢÃÂẤẦẬẨẪĂẮẰẶẲẴ",
				"éèẹẻẽêếềệểễ",
				"ÉÈẸẺẼÊẾỀỆỂỄ",
				"óòọỏõôốồộổỗơớờợởỡ",
				"ÓÒỌỎÕÔỐỒỘỔỖƠỚỜỢỞỠ",
				"úùụủũưứừựửữ",
				"ÚÙỤỦŨƯỨỪỰỬỮ",
				"íìịỉĩ",
				"ÍÌỊỈĨ",
				"đ",
				"Đ",
				"ýỳỵỷỹ",
				"ÝỲỴỶỸ",
				" "
			];
			var replaceAll = function(str, search, replacement) {
				return str.split(search).join(replacement);
			};
			for (var i = 1; i < VietnameseSigns.length; i++) {
				for (var j = 0; j < VietnameseSigns[i].length; j++)
					str = replaceAll(str, VietnameseSigns[i][j], VietnameseSigns[0][i - 1]);
			}
			return str.toLowerCase();
		},
		handleClickLink() {
			console.log("test");
			$(".ass1-header__nav").hide();
		}
	}
}
</script>

<style scoped>
	.user-header {
		position: relative;
		color: #1e1633;
		transition: all .2s ease;
		display: inline-block;
	}
	.user-header:hover {
		color: #3482e2;
	}
	.user-header > span {
		display: inline-block;
		vertical-align: middle;
	}
	.user-header .avatar {
		width: 35px;
		height: 35px;
		margin-right: 4px;
		border-radius: 100%;
		overflow: hidden;
	}
	.user-header .avatar img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	.wrapper-user {
		position: relative;
	}
	.logout {
		right: 0;
		top: 100%;
		position: absolute;
		background-color: #fff;
		padding: 10px 30px;
		min-width: 150px;
		text-align: right;
		border-radius: 3px;
		box-shadow: 0 0 5px 0px rgba(0,0,0,0.1);
		z-index: 0;
		transition: all .3s ease;
		opacity: 0;
		pointer-events: none;
		cursor: pointer;
	}
	.wrapper-user .logout:hover,
	.wrapper-user .user-header:hover + .logout {
		opacity: 1;
		z-index: 2;
		pointer-events: auto;
	}
	.ass1-header__nav > .container ul {
		flex-direction: row;
		flex-wrap: wrap;
	}
	.ass1-header__nav > .container ul li {
		flex: 0 0 25%;
		max-width: 25%;
	}
	.ass1-header__nav > .container ul li:first-child {
		margin-top: 8px;
	}
</style>



