<template>
    <div class="ass1-login">
        <div class="ass1-login__logo">
            <router-link to="/" class="ass1-logo">Nhóm XX</router-link>
        </div>
        <div class="ass1-login__content">
            <p>Đăng nhập</p>
            <div class="ass1-login__form">
                <form>
                    <input v-model="data.email" type="text" class="form-control" placeholder="Email">
                    <div class="ass1-input-copy">
                        <input v-model="data.password" type="password" class="form-control" placeholder="Mật khẩu">
                        <a href="#">Copy</a>
                    </div>
                    <div class="ass1-login__send">
                        <router-link to="/register">Đăng ký một tài khoản</router-link>
                        <button v-on:click="(e) => handleLogin(e)" class="ass1-btn">Đăng nhập</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';

export default {
    name: 'login',
    data() {
        return {
            data: {
                email: '',
                password: '',
            }
        }
    },
    methods: {
        async handleLogin(e) {
            e.preventDefault();
            AlertService.loadingPopup();
            const result = await this.$store.dispatch('login', this.data);
            AlertService.swal.close();
            if(result.ok) {
                AlertService
                    .successPopup(
                        'Success!', 
                        'Đăng nhập thành công, quay lại trang chủ',
                        (e) => {
                        this.$router.push({ path: '/' })
                    });
            } else {
                AlertService.errorPopup('Error!', result.error);
            }
        }
    }
}
</script>

<style>

</style>



