<template>
    <div class="ass1-login">
        <div class="ass1-login__content">
            <p>Đổi mật khẩu</p>
            <div class="ass1-login__form">
                <form>
                    <input v-model="oldPassword" type="password" class="form-control" placeholder="Mật khẩu cũ">
                    <input v-model="newPassword" type="password" class="form-control" placeholder="Mật khẩu mới">
                    <input v-model="reNewPassword" type="password" class="form-control" placeholder="Xác nhận mật khẩu mới">
                    <div class="ass1-login__send justify-content-center">
                        <!-- <a href="dang-ky.html">Đăng ký một tài khoản</a> -->
                        <button @click="(e) => handleChangePassword(e)" type="submit" class="ass1-btn">Thay đổi</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import _ from 'lodash';
export default {
    name: 'change-pass',
    data() {
        return {
            oldPassword: '',
            newPassword: '',
            reNewPassword: '',
        }
    },
    created() {
        // this.$store.dispatch('testtest');
    },
    methods: {
        handleChangePassword(e) {
            e.preventDefault();
            AlertService.loadingPopup();
            let data = {
                oldPassword: this.oldPassword,
                newPassword: this.newPassword,
                reNewPassword: this.reNewPassword,
            }
            this.$store.dispatch('changePassword', data).then(res => {
                // console.log("VUE = ", res);
                AlertService.swal.close();
                if(res.ok) {
                    AlertService.successPopup('Thành công!', res.body.message);
                    this.oldPassword = '';
                    this.newPassword = '';
                    this.reNewPassword = '';
                } else {
                    if(_.isObject(res.error)) {
                        AlertService.errorPopup('Thất bại!', res.error.message);
                    } else {
                        AlertService.errorPopup('Thất bại!', res.error);
                    }
                }
            })
        }
    }  
}
</script>

<style>

</style>



