<template>
    <div>
        <div
            v-if="styleComp"
            v-masonry transition-duration="0.3s" 
            item-selector=".ass1-section__item"
            column-width=".grid-sizer"
            class="ass1-section__wrap row ass1-section__isotope-init"
        >
            <div class="grid-sizer"></div>
            <post-block
                v-masonry-tile 
                v-for="(post, index) in postList" 
                v-bind:key="index" 
                v-bind:post="post"
            />
        </div>
        <div v-if="!styleComp" class="row">
            <post-block
                classColumn="col-lg-12"
                v-for="(post, index) in postList" 
                v-bind:key="index" 
                v-bind:post="post"
            />
        </div>
    </div>
</template>

<script>
import PostBlock from '@/components/homepage/PostBlock'
export default {
    name: 'posts-list',
    components: {
        PostBlock
    },
    props: {
        postList: {
            type: Array,
            required: true
        },
        styleComp: {
            type: Boolean,
            required: false,
            default: true
        }
    },
    data() {
        return {
        }
    },
    mounted() {
    }, 
}
</script>

<style>

</style>



