<template>
    <div class="wrapper-dashboard">
        <h3 class="title">Quản lý thành viên</h3>
        <div class="table-responsive">
            <table class="table table-bordered table-hover">
                <thead class="thead-dark text-center">
                    <tr>
                        <th style="width: 30px">STT</th>
                        <th>ID</th>
                        <th>Gmail</th>
                        <th style="width: 190px">Tên</th>
                        <th style="width: 100px">Giới tính</th>
                        <th style="width: 110px">Kích hoạt</th>
                        <!-- <th style="width: 120px">Hành động</th> -->
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(user, idx) in userList" :key="user.USERID">
                        <td>{{idx + 1}}</td>
                        <td>{{user.USERID}}</td>
                        <td>{{user.email}}</td>
                        <td>{{user.fullname}}</td>
                        <td class="gender">{{user.gender}}</td>
                         <td class="text-center checkbox va-middle">
                            <check-box
                                :index="idx"
                                :status="user.status"
                                labelText=''
                                @onChangeSwitch="onChangeSwitch($event)"
                            />
                        </td>
                         <!-- <td class="text-center">
                            <button class="btn btn-danger"
                                @click="handleDelete(idx, item.PID)">
                                <i class="fa fa-trash" aria-hidden="true"></i>
                            </button>
                            <button class="btn btn-primary">
                               <i class="fa fa-pencil" aria-hidden="true"></i>
                            </button>
                        </td> -->
                    </tr>
                </tbody>
            </table>
        </div>
        <nav aria-label="Page navigation example">
            <ul class="pagination">
                <li class="page-item">
                    <router-link
                        v-if="currentPage > 1"
                        :to="{name: 'manage-user', params: {currentPage: currentPage - 1}}"
                        class="page-link">
                        <i class="fa fa-angle-left" aria-hidden="true"></i>
                    </router-link>
                </li>
                <li 
                    v-for="(item, index) in paginationPage" :key="index" 
                    :class="`page-item ${getactivePage(item)}`">
                    <router-link 
                        class="page-link"
                        :to="{name: 'manage-user', params: {currentPage: item }}"
                    >
                        {{item}}
                    </router-link>
                </li> 
                <!-- <li class="page-item"><a class="page-link" href="#">1</a></li>
                <li class="page-item"><a class="page-link" href="#">2</a></li>
                <li class="page-item"><a class="page-link" href="#">3</a></li> -->
                <li class="page-item">
                    <router-link
                        v-if="currentPage < paginationPage"
                        :to="{name: 'manage-user', params: {currentPage: currentPage + 1}}"
                        class="page-link" 
                        ><i class="fa fa-angle-right" aria-hidden="true"></i>
                    </router-link>
                </li>
            </ul>
        </nav>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js'
import CheckBox from '@/shares/CheckBox'
import { mapGetters } from 'vuex';
import _ from 'lodash';

export default {
    name: 'manage-user',
    data() {
        return {
            userList: [],
            pageSize: 4,
            currentPage: this.$route.params.currentPage,
            totalPage: 0
        }
    },
    components: {
        CheckBox
    },
    watch: {
        '$route' (to, from) {
            console.log(to);
            this.currentPage = to.params.currentPage;
            this.fetchDataUsers(this.currentPage);
        }
    },
    created() {
        // this.fetchDataPosts();
        this.checkAdmin();
        this.fetchDataUsers(this.currentPage);
    },
    methods: {
        async checkAdmin() {
            const result = await this.$store.dispatch('checkAdmin');
            if(!result.ok) {
                // AlertService.errorPopup("Lỗi!", "Không phải Admin");
                this.$router.push("/");
            }
        },
        async fetchDataUsers(page) {
            this.currentPage = page;
            const data = {
                pageSize: this.pageSize,
                currentPage: this.currentPage
            }
            // console.log("data", data);
            AlertService.loadingPopup();
            const result = await this.$store.dispatch('getListUserPagination',data);
            if(result.ok) {
                this.totalPage = result.body.total;
                this.userList = result.body.users;
                // this.userList = [...this.userList, ...result.body.users];
            }
            AlertService.swal.close();
            
        },
        getactivePage(page) {
            if(page == this.currentPage) return 'active';
            return '';
        },

        onChangeSwitch(data) {
            let index = data[0].index
            let user = this.userList[index]
            AlertService
            .warningPopup(
                'Cảnh báo!',
                `Bạn có muốn thay đổi trạng thái bài viết có ID là ${user.USERID} không?`, async (cb) => {
                if(cb.value) {
                    AlertService.loadingPopup();
                    const response = await this.$store.dispatch('toggleStatusUser', user.USERID);
                    // AlertService.swal.close();
                    if(response.ok) {
                        const _response = await this.fetchDataUsers(this.currentPage);
                        AlertService.successPopup("Thành công!", response.body);
                    } else if (typeof response.error == 'string'){
                        AlertService.errorPopup('Lỗi!', response.error);
                    } else {
                        AlertService.errorPopup('Lỗi!', response.error.message);
                    }
                }
            })
        },
        formatDate(value) {
            var d = new Date(value);
            var day = (d.getDate() <= 9)? "0" + d.getDate() : d.getDate();
            var month = (d.getMonth() + 1 <= 9)? "0" + (d.getMonth() + 1): (d.getMonth() + 1);
            var year = (d.getFullYear() <= 9)? "0" + d.getFullYear() : d.getFullYear();
            var hour = (d.getHours() <= 9)? "0" + d.getHours() : d.getHours();
            var minute = (d.getMinutes() <= 9)? "0" + d.getMinutes() : d.getMinutes();
            var second = (d.getSeconds() <= 9)? "0" + d.getSeconds() : d.getSeconds();
            // return month + "/" + day + "/" + year;
            return `${day}/${month}/${year}, ${hour}:${minute}:${second}''`;
        },
        handleDelete(idx, pid) {
            AlertService
            .warningPopup(
            'Cảnh báo!',
            `Bạn có muốn xoá bài viết có ID là ${pid} không?`, async (cb) => {
                AlertService.loadingPopup();
                const response = await this.$store.dispatch('deletePostByPID', pid);
                AlertService.swal.close();
                if(response.ok) {
                    const _response = await this.fetchDataPosts();
                    AlertService.successPopup("Thành công!", response.body);
                } else if (typeof response.error == 'string'){
                    AlertService.errorPopup('Lỗi!', response.error);
                } else {
                    AlertService.errorPopup('Lỗi!', response.error.message);
                }
            });
        }
    },
    computed: {
         ...mapGetters([
            'currentUser'
        ]),
        paginationPage() {
            let data = parseInt(this.totalPage, 10) / this.pageSize;
            if(Math.floor(data) === data) {
                return Math.floor(data);
            }
            return Math.floor(data) + 1;
        }
    }
}
</script>

<style scoped>
    .table tr td {
        vertical-align: middle;
    }
    .table {
        /* table-layout: fixed; */
        margin-top: 30px;
        background-color: #fff;
    }
    .wrapper-dashboard {
        padding: 30px 0;
    }
    .title {
        text-align: center;
    }
    .checkbox.va-middle {
        font-size: 0;
    }
    .va-middle {
        vertical-align: middle;
    }
    .btn.btn-danger {
        min-width: auto;
        color: #fff;
        background-color: #dc3545;
        border-color: #dc3545;
    }
    .btn.btn-danger:hover {
        color: #fff;
        background-color: #c82333;
        border-color: #bd2130;
    }
    .btn.btn-primary {
        min-width: auto;
        color: #fff;
        background-color: #007bff;
        border-color: #007bff;
    }
    .gender {
        text-align: center;
        text-transform: capitalize;
    }
</style>



