<template>
    <div>
        <div class="ass1-head-user" v-if="user">
            <div class="ass1-head-user__content">
                <div class="ass1-head-user__image"><router-link :to="{ name: 'user-detail', params: {userid: user.USERID} }"><img :src="user.profilepicture || 'static/images/default-avatar.png'" alt="avatar"></router-link></div>
                <div class="ass1-head-user__info">
                    <div class="ass1-head-user__info-head">
                        <div class="ass1-head-user__name">
                            <span>{{user.fullname}}</span> 
                            <i v-if="user.status == '1'"><img src="static/fonts/emotion/svg/Verified.svg" alt="check"></i>
                        </div>
                        <div class="w-100"></div>
                        <a v-if="!iscurrentUser" href="#" class="ass1-head-user__btn-follow ass1-btn">Theo dõi</a>
                        <router-link v-if="iscurrentUser" to="/change-password" class="ass1-head-user__btn-follow ass1-btn">Đổi mật khẩu</router-link>
                        <router-link v-if="iscurrentUser" to="/dashboard/profile" class="ass1-head-user__btn-follow ass1-btn">Profile</router-link>
                        <router-link v-if="iscurrentUser" to="/dashboard/posts" class="ass1-head-user__btn-follow ass1-btn">Quản lý bài viết</router-link>
                        <router-link v-if="iscurrentUser && user.permission == 'admin'" to="/admin/users/page=1" class="ass1-head-user__btn-follow ass1-btn">Quản lý thành viên</router-link>
                        <!-- <a href="#" class="ass1-head-user__btn-options ass1-btn-icon"><i class="icon-Options"></i></a> -->
                    </div>
                    <div class="ass1-head-user__info-statistic">
                        <div class="ass1-btn-icon"><i class="icon-Post"></i><span>Bài viết: {{postList.length}}</span></div>
                        <div class="ass1-btn-icon"><i class="icon-Followers"></i><span>Theo dõi: 0</span></div>
                        <div class="ass1-btn-icon"><i class="icon-Following"></i><span>Đang theo dõi: 0</span></div>
                        <!-- <div class="ass1-btn-icon"><i class="icon-Upvote"></i><span>Up Vote: 999999</span></div> -->
                    </div>
                    <p v-if="user">{{user.description}}</p>
                </div>
            </div>
        </div>
        <posts-list v-if="postList.length" v-bind:postList="postList"/>
    </div>
</template>

<script>
import PostsList from '@/components/homepage/PostsList'
import { mapGetters } from 'vuex';
import AlertService from '@/plugins/AlertService.js';

export default {
    name: 'user-detail',
    components: {
        PostsList
    },
    data() {
        return {
            routeUserId: this.$route.params.userid,
            user: null,
            postList: []
        }
    }, 
    watch: {
    '$route' (to, from) {
      // this.disableMainHeader();
        this.routeUserId = to.params.userid;
        this.fetchDataPosts();
        this.fetchDataUser();
    }
    },
    created() {
        this.fetchDataPosts();
        this.fetchDataUser();
    },
    methods: {
        async fetchDataUser() {
            AlertService.loadingPopup();
            const result =  await this.$store.dispatch('getUserByID', this.routeUserId)
            if(result.ok) {
                this.user = result.body
            } else {
                if(typeof result.error == 'string') {
                    AlertService.errorPopup('Lỗi!', result.error, () => {
                        this.$router.push("/");
                    })
                } else {
                    // console.log(result.error.response);
                    if(result.error.response.status == 401) {
                        this.$store.dispatch('disableUser');
                    }
                }
            }
            AlertService.swal.close();
        },
        fetchDataPosts() {
            AlertService.loadingPopup();
            this.postList = [];
            this.$store.dispatch('getListPostByUserID', this.routeUserId).then(result => {
                // console.log(".vue",result);
                if(result.ok) {
                    this.postList = result.body
                } else {
                    console.log(result.error.response);
                    if(result.error.response.status == 401) {
                        this.$store.dispatch('disableUser');
                    }
                }
                AlertService.swal.close();
            });
        }
    },
    computed: {
        ...mapGetters([
            'currentIDUser'
        ]),
        iscurrentUser() {
            if(this.routeUserId == this.currentIDUser) return true;
            return false;
        }
    }
}
</script>

<style>

</style>



