// import Cookies from 'js-cookie';

export default {
    accessToken(state) {
        // return Cookies.get('access_token');
        return state.access_token;
    },
    isAuthenticated(state) {
        // return !!Cookies.get('access_token');
        return !!state.access_token;
    },
    currentUser(state) {
        // return Cookies.getJSON('current_user');
        return state.current_user;
    },
    currentIDUser(state) {
        // let current_user = Cookies.getJSON('current_user');
        if(state.current_user) return state.current_user.USERID;
        return null;
    },
    allCategories(state) {
        return state.category;
    }
}
  


