<template>
    <div :class="`ass1-section__item ${classColumn}`">
        <div class="ass1-section">
            <div class="ass1-section__head">
                <router-link :to="{ name: 'user-detail', params: {userid: post.USERID} }" class="ass1-section__avatar ass1-avatar"><img :src="post.profilepicture || 'static/images/default-avatar.png'" alt="author"></router-link>
                <div>
                    <router-link 
                        :to="{ 
                            name: 'user-detail', 
                            params: {userid: post.USERID} 
                        }" class="ass1-section__name">
                        <span v-if="query === null">{{post.fullname}}</span>
                        <span v-if="query !== null" v-html="formatContent(post.fullname)"></span>
                    </router-link>
                    <span class="ass1-section__passed">{{dateFormat}}</span>
                </div>
            </div>
            <div class="ass1-section__content">
                
                <p v-if="query === null">{{post.post_content}}</p>
                <p v-if="query !== null" v-html="formatContent(post.post_content)"></p>
                
                <router-link :to="{ name: 'post-detail', params: {id: post.PID} }" class="ass1-section__btn-upvote ass1-btn-icon">Xem thêm</router-link>
                <div class="ass1-section__image">
                    <router-link :to="{ name: 'post-detail', params: {id: post.PID} }"><img :src="post.url_image" alt=""></router-link>
                </div>
            </div>
            <div class="ass1-section__footer">
                <!-- <a href="#" class="ass1-section__btn-upvote ass1-btn-icon"><i class="icon-Upvote"></i></a> -->
                <!-- <a href="#" class="ass1-section__btn-downvote ass1-btn-icon"><i class="icon-Downvote"></i></a> -->
                <!-- <a href="#" class="ass1-section__btn-like ass1-btn-icon"><i class="icon-Favorite_Full"></i><span>1,274</span></a> -->
                <a href="#" class="ass1-section__btn-comment ass1-btn-icon"><i class="icon-Comment_Full"></i><span>{{post.count || 0}}</span></a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'post-block',
    props: {
        post: {
            type: Object,
            required: true,
        },
        classColumn: {
            type: String,
            default: 'col-lg-6'
        }
    },
    data() {
        return {
            routerName: this.$route.name,
            query: null,
        }
    },
    watch: {
        '$route' (to, from) {
            if(to.name === 'search-post') {
                this.query = to.params.query;
            } else this.query = null;
        }
    },
    created() {
        if(this.routerName === 'search-post') {
            this.query = this.$route.params.query;
        } else this.query = null;
    },
    methods: {
        setTimeAgo(_date){
            var vm = this;
            var _time = new Date();
            _time = new Date(_time);
            vm.timeAgo = _time.getTime();
            var sub = (vm.timeAgo - _date)/1000;
            var re_val = "";
            if (sub >= 0 && sub < 1){
                return "Vừa xong";
            }
            else if (sub <= 60){
                var temp_str = " giây ";
                re_val = Math.floor(sub).toString() + temp_str + "trước";
            }
            else if (sub <= 3600){
                var temp_str = " phút ";
                re_val = Math.floor(sub/60).toString() + temp_str + "trước";
            }
            else if (sub <= 86400){
                var temp_str = " giờ ";
                re_val = Math.floor(sub/3600).toString() + temp_str + "trước";
            }
            else {
                var temp_str = " ngày ";
                re_val = Math.floor(sub/86400).toString() + temp_str + "trước";
            }
            return re_val;
        },
        formatContent(str) {
            let query = this.query.toLowerCase();
            str = str.toLowerCase();
            return str.replace(query, `<span class="highlight">${query}</span>`);
        }
    },
    computed: {
        dateFormat: function() {
            var d = new Date(this.post.time_added);
            d = new Date(d).getTime();
            return this.setTimeAgo(d);
        }
    }
}
</script>

<style scoped>
    .ass1-section__avatar.ass1-avatar {
        position: relative;
        border-radius: 10%;
        overflow: hidden;
    }
    .ass1-section__avatar.ass1-avatar img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .ass1-section__name span {
        text-transform: capitalize;
    }
    .ass1-section__content > p:first-letter {
        text-transform: uppercase;
    }
</style>
<style>
    .highlight {
        background-color: yellow;
        color: black;
    }
</style>



