<template>
    <div>
        <!--content head-->
        <div class="ass1-content-head ass1-content-head__flex-off">
            <div class="row">
                <div class="col-lg-8">
                </div>
                <div class="col-lg-4">
                    <div class="ass1-content-head__right-col">
                        <div class="ass1-content-head__text">
                            <div>Bài Post gần đây</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--sections-->
        <div class="row">
            <div class="col-lg-8">
                <!--section-->
                <div class="ass1-section" v-if="dataPost">
                    <div class="ass1-section__head">
                        <router-link :to="{ name: 'user-detail', params: {userid: dataPost.post.USERID} }"
                            class="ass1-section__avatar ass1-avatar">
                            <img :src="author.profilepicture || 'static/images/default-avatar.png'" alt="author">
                        </router-link>
                        <div v-if="author">
                            <router-link :to="{ name: 'user-detail', params: {userid: dataPost.post.USERID} }" 
                                class="ass1-section__name">{{author.fullname}}</router-link>
                            <span class="ass1-section__passed">{{dateFormat}}</span>
                        </div>
                        <router-link :to="{ name: 'user-detail', params: {userid: dataPost.post.USERID} }"  class="ass1-section__link ass1-btn-icon"><i class="icon-Link"></i></router-link>
                    </div>
                    <div class="ass1-section__content">
                        <p>{{dataPost.post.post_content}}</p>
                        <div class="ass1-section__image">
                            <router-link :to="{ name: 'user-detail', params: {userid: dataPost.post.USERID} }" ><img :src="dataPost.post.url_image" alt="post detail image"></router-link>
                        </div>
                    </div>
                    <div class="ass1-section__footer">
                        <a href="#" class="ass1-section__btn-upvote ass1-btn-icon"><i class="icon-Upvote"></i></a>
                        <a href="#" class="ass1-section__btn-downvote ass1-btn-icon"><i class="icon-Downvote"></i></a>
                        <!-- <a href="#" class="ass1-section__btn-repost ass1-btn-icon"><i class="icon-Repost"></i></a> -->
                        <a href="#" class="ass1-section__btn-like ass1-btn-icon"><i class="icon-Favorite_Full"></i><span>0</span></a>
                        <a href="#" class="ass1-section__btn-comment ass1-btn-icon"><i class="icon-Comment_Full"></i><span>{{comments.length}}</span></a>

                    </div>
                    <div class="ass1-section__feeling">
                        <!-- <div class="ass1-feeling">
                            <a href="#" rel="tooltip" data-original-title="Happy"><img src="static/fonts/emotion/svg/Happy.svg" alt=""><span>126</span></a>
                            <a href="#" rel="tooltip" data-original-title="Lol"><img src="static/fonts/emotion/svg/LOL.svg" alt=""><span>256</span></a>
                            <a href="#" rel="tooltip" data-original-title="Surprised"><img src="static/fonts/emotion/svg/Surprised.svg" alt=""><span>741</span></a>
                            <a href="#" rel="tooltip" data-original-title="Sad"><img src="static/fonts/emotion/svg/Sad.svg" alt=""><span>2K</span></a>
                            <a href="#" rel="tooltip" data-original-title="Tongue Out"><img src="static/fonts/emotion/svg/Tongue_Out.svg" alt=""><span>245</span></a>
                            <a href="#" rel="tooltip" data-original-title="No words"><img src="static/fonts/emotion/svg/No_words.svg" alt=""><span>237</span></a>
                            <a href="#" rel="tooltip" data-original-title="Love"><img src="static/fonts/emotion/svg/Love.svg" alt=""><span>1,236</span></a>
                            <a href="#" rel="tooltip" data-original-title="Blushing"><img src="static/fonts/emotion/svg/Blushing.svg" alt=""><span>365</span></a>
                            <a href="#" rel="tooltip" data-original-title="Cool"><img src="static/fonts/emotion/svg/Cool.svg" alt=""><span>412</span></a>
                            <a href="#" rel="tooltip" data-original-title="Angry"><img src="static/fonts/emotion/svg/Angry.svg" alt=""><span>478</span></a>
                        </div> -->
                        <ul>
                            <li>Danh mục: </li>
                            <li v-for="item in dataPost.categories" :key="item.TAG_ID">
                                <router-link :to="{
                                    name: 'categories',
                                    params: {
                                        text: stringToURL(item.tag_value),
                                        id:item.tag_index
                                    }
                                }">{{item.tag_value}}</router-link>
                            </li>
                        </ul>
                    </div>
                </div>

                <!-- add comment -->
                <add-comment 
                    v-on:addCommentToggle="addCommentToggle($event)"
                    v-bind:routePostId="routePostId" />

                <!-- show list comment -->
                <list-comment
                    v-bind:comments="comments" />
            </div>
            <div class="col-lg-4">
               <posts-list 
                    v-bind:styleComp="false"
                    v-bind:postList="recentPost"/>
            </div>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import ListComment from '@/components/postdetail/ListComment'
import AddComment from '@/components/postdetail/AddComment'
// import LatestPost from '@/components/postdetail/LatestPost'
import PostsList from '@/components/homepage/PostsList'
import { mapGetters } from 'vuex';
export default {
    name: 'post-detail',
    components: {
        ListComment,
        AddComment,
        PostsList
    },
    data() {
        return {
            routePostId: this.$route.params.id,
            comments: [],
            dataPost: null,
            author: null,
            recentPost: []
        }
    },
    watch: {
        '$route' (to, from) {
            this.routePostId = to.params.id;
            this.fetchDataPost();
            this.fetchDataComment();
            this.fetchRecentPost();
        }
    },
    created() {
        this.fetchDataComment();
        this.fetchDataPost();
        this.fetchRecentPost();
    },
    computed: {
        ...mapGetters([
            'isAuthenticated'
        ]),
        dateFormat: function() {
            if(this.dataPost) {
                var d = new Date(this.dataPost.post.time_added)
                d = new Date(d).getTime();
                return this.setTimeAgo(d);
            }
            return null;
        }
    },
    methods: {
        addCommentToggle(data) {
            // console.log("addCommentToggle", data);
            this.fetchDataComment();
        },
        fetchDataComment() {
            if(this.routePostId) {
                this.comments = [];
                this.$store.dispatch('getCommentPost', this.routePostId)
                .then(res => {
                    if(res.ok) {
                        this.comments = res.body;
                    }
                });
            }   
        },
        async fetchDataPost() {
            AlertService.loadingPopup();
            if(this.routePostId) {
                const result =  await this.$store.dispatch('getPostByPostID', this.routePostId)
                if(result.ok) {
                    await this.fetchDataUser(result.body.data.post.USERID);
                    this.dataPost = result.body.data;
                    AlertService.swal.close();
                } else {
                    AlertService.swal.close();
                    AlertService.errorPopup("Lỗi!", result.error, () => {
                        this.$router.push("/");
                    });
                    // console.log(result.error.response);
                    if(result.error.response.status == 401) {
                        this.$store.dispatch('disableUser');
                    }
                }
            }
        },
        async fetchDataUser(routeUserId) {
            const result =  await this.$store.dispatch('getUserByID', routeUserId)
            if(result.ok) {
                this.author = result.body
            } else {
                // console.log(result.error.response);
                if(result.error.response.status == 401) {
                    this.$store.dispatch('disableUser');
                }
            }
        },
        async fetchRecentPost() {
            const data = {
                currentPage: 1,
                pageSize: 3
            };
            this.recentPost=[]
            const res = await this.$store.dispatch('getListPostPagination', data);
            if(res.ok) {
                this.recentPost = res.body;
            }
        },
        setTimeAgo(_date){
            var vm = this;
            var _time = new Date();
            _time = new Date(_time);
            vm.timeAgo = _time.getTime();
            var sub = (vm.timeAgo - _date)/1000;
            var re_val = "";
            if (sub >= 0 && sub < 1){
                return "Vừa xong";
            }
            else if (sub <= 60){
                var temp_str = " giây ";
                re_val = Math.floor(sub).toString() + temp_str + "trước";
            }
            else if (sub <= 3600){
                var temp_str = " phút ";
                re_val = Math.floor(sub/60).toString() + temp_str + "trước";
            }
            else if (sub <= 86400){
                var temp_str = " giờ ";
                re_val = Math.floor(sub/3600).toString() + temp_str + "trước";
            }
            else {
                var temp_str = " ngày ";
                re_val = Math.floor(sub/86400).toString() + temp_str + "trước";
            }
            return re_val;
        },
        stringToURL(str) {
            console.log(str);
			var VietnameseSigns = [ "aAeEoOuUiIdDyY-",
				"áàạảãâấầậẩẫăắằặẳẵ",
				"ÁÀẠẢÃÂẤẦẬẨẪĂẮẰẶẲẴ",
				"éèẹẻẽêếềệểễ",
				"ÉÈẸẺẼÊẾỀỆỂỄ",
				"óòọỏõôốồộổỗơớờợởỡ",
				"ÓÒỌỎÕÔỐỒỘỔỖƠỚỜỢỞỠ",
				"úùụủũưứừựửữ",
				"ÚÙỤỦŨƯỨỪỰỬỮ",
				"íìịỉĩ",
				"ÍÌỊỈĨ",
				"đ",
				"Đ",
				"ýỳỵỷỹ",
				"ÝỲỴỶỸ",
				" "
			];
			var replaceAll = function(str, search, replacement) {
				return str.split(search).join(replacement);
            };
			for (var i = 1; i < VietnameseSigns.length; i++) {
				for (var j = 0; j < VietnameseSigns[i].length; j++)
					str = replaceAll(str, VietnameseSigns[i][j], VietnameseSigns[0][i - 1]);
			}
			return str.toLowerCase();
		},
    }
}
</script>

<style scoped>
    .ass1-section__avatar.ass1-avatar {
        position: relative;
        border-radius: 10%;
        overflow: hidden;
    }
    .ass1-section__avatar.ass1-avatar img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .ass1-section__feeling li {
        display: inline-block;
        margin-right: 10px;
        margin-top: 10px;
    }
    .ass1-section__feeling li a {
        display: block;
        font-size: 13px;
        padding: 5px 15px;
        border-radius: 15px;
        border: 1px solid rgba(204, 204, 204, 0.5);
        background-color: #fff;
        box-shadow: 0 0 0 0 rgba(0,0,0,0.1);
        transition: all .3s ease;
    }
    .ass1-section__feeling li a:hover {
        border-color: rgba(204, 204, 204, 0.3);
        box-shadow: 1px 1px 20px 0px rgba(0,0,0,0.1);
    }
</style>


