<template>
    <div class="comp-switch">
        <input
            type="checkbox" 
            :id="`id-switch-${randomKey}`"
            v-model="status"
            true-value="1"
            false-value="0"
            class="switch-input" />
        <label 
            @click="onChangeSwitch($event)"
            class="switch-label">{{labelText}}</label>
    </div>
</template>

<script>
export default {
    name: 'check-box',
    props: [
        'index',
        'status',
        'labelText'
    ],
    data() {
        return {
            randomKey: Math.round(Math.random() * 9999)
        }
    },
    methods: {
        onChangeSwitch(e) {
            e.preventDefault();
            this.$emit('onChangeSwitch', [{
                index: this.index
            }]);
        }
    }
}
</script>

<style scoped>
.comp-switch {
    position: relative;
    display: inline-block
}
.switch-input {
    display: none;
}
.switch-label {
    position: relative;
    display: block;
    min-width: 112px;
    cursor: pointer;
    font-weight: 500;
    text-align: left;
    padding: 0 0 0 37px;
    line-height: 20px;
    min-width: 37px;
    min-height: 22px;
    margin: 0;
}
.switch-label:before, .switch-label:after {
    content: "";
    position: absolute;
    margin: 0;
    outline: 0;
    top: 50%;
    -ms-transform: translate(0, -50%);
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.switch-label:before {
    left: 1px;
    width: 34px;
    height: 14px;
    background-color: #9E9E9E;
    border-radius: 8px;
}
.switch-label:after {
    left: 0;
    width: 20px;
    height: 20px;
    background-color: #FAFAFA;
    border-radius: 50%;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.14), 0 2px 2px 0 rgba(0, 0, 0, 0.098), 0 1px 5px 0 rgba(0, 0, 0, 0.084);
}
.switch-input:checked + .switch-label:before {
    background-color: rgba(0, 162, 147, 0.6);
}
.switch-input:checked + .switch-label:after {
    background-color: #009688;
    -ms-transform: translate(80%, -50%);
    -webkit-transform: translate(80%, -50%);
    transform: translate(80%, -50%);
}
</style>



