<template>
  <div id="app">
    <app-header v-if="showMainHeader"/>
    <main>
      <div class="container">
        <transition name="fade">
          <router-view/>
        </transition>
      </div>
    </main>
    <app-footer v-if="showMainFooter"/>
  </div>
</template>

<script>
import AppHeader from '@/shares/AppHeader.vue'
import AppFooter from '@/shares/AppFooter.vue'
export default {
  name: 'App',
  components: {
    AppHeader,
    AppFooter
  },
  data() {
    return {
      showMainHeader: true,
      showMainFooter: false,
    }
  },
  watch: {
    '$route' (to, from) {
      // this.disableMainHeader();
      this.enableFooter();
    }
  },
  mounted() {
    // this.disableMainHeader();
    this.enableFooter();
  },
  methods: {
    disableMainHeader() {
      let _h = this.$route.name;
      let arr = ['login', 'register'];
      if(arr.indexOf(_h) != -1) {
        this.showMainHeader = false;
      } else {
        this.showMainHeader = true;
      }
    },
    enableFooter() {
      let _h = this.$route.name;
      let arr = ['login', 'register', 'change-password', 'edit-profile'];
      if(arr.indexOf(_h) != -1) {
        this.showMainFooter = true;
      } else {
        this.showMainFooter = false;
      }
    }
  }
}
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



