<template>
  <div>
    <div class="ass1-content-head">
		</div>
    <div class="row">
      <div class="col-lg-8">
        <div v-if="postList.length != 0">
          <posts-list 
            v-bind:styleComp="false"
            v-bind:postList="postList"/>
          <button 
            v-on:click="loadNextPage"
            :class="classLoader"
            class="load-more ass1-btn">
              <span>Xem thêm</span>
              <img v-show="loading" src="/static/loader.gif" alt="loader">
          </button>
        </div>
      </div>
      <div class="d-none d-lg-block col-lg-4">
        <div class="ass1-content-head__t">
          <div>Bài viết gần đây của bạn.</div>
        </div>
        <div v-if="!isAuthenticated">
          Vui lòng đăng nhập để xem mục này <router-link to="/login">Đăng nhập</router-link>
        </div>
        <posts-list
          v-if="isAuthenticated"
          v-bind:styleComp="false"
          v-bind:postList="recentlyPost"/>
      </div>
    </div>
    
  </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js'
import PostsList from '@/components/homepage/PostsList'
import _ from 'lodash';
import { mapGetters } from 'vuex';
export default {
  name: 'Homepage',
  components: {
    PostsList
  },  
  data () {
    return {
      postList: [],
      currentPage: 0,
      pageSize: 3,
      recentlyPost: [],
      loading: false,
      init: true,
    }
  },
  created() {
    this.fetchDataSearch();
    this.fetchDataRecentlyPosts();
  },
  computed: {
    classLoader() {
      return {
        'loading': this.loading
      }
    },
    ...mapGetters([
      'isAuthenticated'
    ])
  },
  methods: {
    async fetchDataSearch() {
      if(this.init) { 
        AlertService.loadingPopup()
      }

      this.loading = true;
      this.currentPage = this.currentPage + 1;
      const data = {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      };
      const res = await this.$store.dispatch('getListPostPagination', data);
      // console.log(res);
      if(res.ok) {
        // this.postList.push( res.body );
        const newArray = [...this.postList, ...res.body];
        this.postList = newArray;
        this.loading = false;
      } else { this.loading = false; }
      if(this.init) {
        this.init = false;
        AlertService.swal.close();
      }
    },
    fetchDataRecentlyPosts() {
      this.recentlyPost = [];
      const userid = this.$store.getters.currentIDUser;
      if(!userid) return;
      this.$store.dispatch('getListPostByUserID', userid).then(result => {
        // console.log(".vue",result);
        if(result.ok) {
          this.recentlyPost = result.body.slice(0,3);
        } else {
          if(result.error.response.status == 401) {
            this.$store.dispatch('disableUser');
          }
        }
      });
    },
    loadNextPage() {
      this.fetchDataSearch();
    }
  }
}
</script>

<style scoped>
  .load-more {
    min-width: 150px;
    margin: 0 auto;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 100px;
    position: relative;
  }
  .ass1-content-head__t {
    font-weight: 700;
    margin-top: -30px;
    margin-bottom: 10px;
  }
  .load-more span {
    transform: scale(1);
    transition: all .3s ease;
  }
  .load-more img {
    position: absolute;
    max-height: 45px;
    top: 50%;
    left: 50%;
    width: auto;
    transform: translate(-50%, -50%);
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
  }
  .load-more.loading img {
    opacity: 1;
    visibility: visible;
  }
  .load-more.loading span {
    transform: scale(0);
  }
  .load-more.ass1-btn:hover {
    color: #ffffff;
    background-color: #3482e2;
  }
  :focus {
    outline: none;
  }
</style>



