<template>
    <div class="wrapper-dashboard">
        <h3 class="title">Quản lý bài viết</h3>
        <div class="table-responsive">
            <table class="table table-bordered table-hover">
                <thead class="thead-dark text-center">
                    <tr>
                        <th style="width: 30px">STT</th>
                        <th>ID</th>
                        <th>Nội dung</th>
                        <th style="width: 190px">Ngày đăng</th>
                        <th style="width: 110px">Trạng thái</th>
                        <th style="width: 120px">Hành động</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in postList" :key="item.PID">
                        <td class="text-center">{{idx+1}}</td>
                        <td class="text-center">{{item.PID}}</td>
                        <td v-html="item.post_content"></td>
                        <td>{{formatDate(item.time_added)}}</td>
                        <td class="text-center checkbox va-middle">
                            <check-box
                                :index="idx"
                                :status="item.status"
                                labelText=''
                                @onChangeSwitch="onChangeSwitch($event)"
                            />
                        </td>
                        <td class="text-center">
                            <button class="btn btn-danger"
                                @click="handleDelete(idx, item.PID)">
                                <i class="fa fa-trash" aria-hidden="true"></i>
                            </button>
                            <button class="btn btn-primary"
                                @click="handleEdit(item.PID)">
                               <i class="fa fa-pencil" aria-hidden="true"></i>
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js'
import CheckBox from '@/shares/CheckBox'
import { mapGetters } from 'vuex';
import _ from 'lodash';

export default {
    name: 'manage-post',
    data() {
        return {
            postList: []
        }
    },
    components: {
        CheckBox
    },
    created() {
        this.fetchDataPosts();
    },
    methods: {
        async fetchDataPosts() {
            this.postList = [];
            let userid = this.currentUser.USERID;
            const result = await this.$store.dispatch('getListPostByUserID', userid);
            if(result.ok) {
                this.postList = result.body
            } else {
                console.log(result.error.response);
                if(result.error.response.status == 401) {
                    this.$store.dispatch('disableUser');
                }
            }
        },
        toggleStatus(index) {
            if(this.postList[index].status === '1')
                this.postList[index].status = '0';
            else this.postList[index].status = '1';
        },
        onChangeSwitch(data) {
            let index = data[0].index
            let post = this.postList[index]
            AlertService
            .warningPopup(
                'Cảnh báo!',
                `Bạn có muốn thay đổi trạng thái bài viết có ID là ${post.PID} không?`, async (cb) => {
                if(cb.value) {
                    AlertService.loadingPopup();
                    const response = await this.$store.dispatch('toggleStatusPost', post.PID);
                    AlertService.swal.close();
                    if(response.ok) {
                        const _response = await this.fetchDataPosts();
                        AlertService.successPopup("Thành công!", response.body);
                    } else if (typeof response.error == 'string'){
                        AlertService.errorPopup('Lỗi!', response.error);
                    } else {
                        AlertService.errorPopup('Lỗi!', response.error.message);
                    }
                }
            })
        },
        formatDate(value) {
            var d = new Date(value);
            var day = (d.getDate() <= 9)? "0" + d.getDate() : d.getDate();
            var month = (d.getMonth() + 1 <= 9)? "0" + (d.getMonth() + 1): (d.getMonth() + 1);
            var year = (d.getFullYear() <= 9)? "0" + d.getFullYear() : d.getFullYear();
            var hour = (d.getHours() <= 9)? "0" + d.getHours() : d.getHours();
            var minute = (d.getMinutes() <= 9)? "0" + d.getMinutes() : d.getMinutes();
            var second = (d.getSeconds() <= 9)? "0" + d.getSeconds() : d.getSeconds();
            // return month + "/" + day + "/" + year;
            return `${day}/${month}/${year}, ${hour}:${minute}:${second}''`;
        },
        handleDelete(idx, pid) {
            AlertService
            .warningPopup(
            'Cảnh báo!',
            `Bạn có muốn xoá bài viết có ID là ${pid} không?`, async (cb) => {
                AlertService.loadingPopup();
                const response = await this.$store.dispatch('deletePostByPID', pid);
                AlertService.swal.close();
                if(response.ok) {
                    const _response = await this.fetchDataPosts();
                    AlertService.successPopup("Thành công!", response.body);
                } else if (typeof response.error == 'string'){
                    AlertService.errorPopup('Lỗi!', response.error);
                } else {
                    AlertService.errorPopup('Lỗi!', response.error.message);
                }
            });
        },
        handleEdit(pid) {
            this.$router.push({
                name: 'edit-post',
                params: {
                    postid: pid
                }
            })
        }
    },
    computed: {
         ...mapGetters([
            'currentUser'
        ])
    }
}
</script>

<style scoped>
    .table tr td {
        vertical-align: middle;
    }
    .table {
        /* table-layout: fixed; */
        margin-top: 30px;
        background-color: #fff;
    }
    .wrapper-dashboard {
        padding: 30px 0;
    }
    .title {
        text-align: center;
    }
    .checkbox.va-middle {
        font-size: 0;
    }
    .va-middle {
        vertical-align: middle;
    }
    .btn.btn-danger {
        min-width: auto;
        color: #fff;
        background-color: #dc3545;
        border-color: #dc3545;
    }
    .btn.btn-danger:hover {
        color: #fff;
        background-color: #c82333;
        border-color: #bd2130;
    }
    .btn.btn-primary {
        min-width: auto;
        color: #fff;
        background-color: #007bff;
        border-color: #007bff;
    }
</style>



