<template>
    <div class="ass1-add-comment">
        <form action="#">
            <textarea
                rows="3"
                v-model="comment" 
                type="text" 
                class="form-control ttg-border-none" 
                placeholder="Thêm một bình luận"></textarea>
        </form>
        <div class="ass1-add-comment__content">
            <a 
                href="#" 
                class="ass1-add-comment__btn-save ass1-btn-icon"
                v-on:click="(e) => handleComment(e)">
                <span>{{maxLength - comment.length}}</span>
                <i class="icon-Submit_Tick"></i>
            </a>
        </div>
    </div>
</template>

<script>
import AlertService from '@/plugins/AlertService.js';
import { mapGetters } from 'vuex';
export default {
    name: 'add-comment',
    props: {
        routePostId: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            comment: '',
            maxLength: 180
        }
    },
    watch: {
        comment: function(newValue, oldValue) {
            if(newValue.length > this.maxLength) {
                this.comment = oldValue;
            }
        }
    },
    computed: {
        ...mapGetters([
            'currentUser',
            'isAuthenticated'
        ])
    },
    methods: {
        async handleComment(e) {
            e.preventDefault();
            if(!this.isAuthenticated && !this.currentUser) {
                AlertService.warningPopup('Cảnh báo!', 'Bạn cần đăng nhập để thực hiện tính năng này!', (cb) => {
                    if(cb.value) {
                        this.$router.push('/login');
                    }
                });
                return;
            }
            if( this.comment.length != 0 ) {
                let data = {
                    postid: this.routePostId,
                    comment: this.comment
                }
                const res = await this.$store.dispatch('postComment', data);
                // console.log(res);
                if(res.ok) {
                    AlertService.successPopup('Thành công!', 'Đăng bình luận thành công!');
                    this.comment = '';
                    this.$emit('addCommentToggle', true);
                } else {
                    let message = '';
                    
                    if(typeof res.error == 'string') message = res.error;
                    else message = res.error.response.data.error;

                    AlertService.errorPopup('Lỗi!', message);
                }

            } else {
                AlertService.warningPopup("Cảnh báo!", "Chưa nhập bình luận!");
            }
        }
    }
}
</script>

<style scoped>
    .ass1-add-comment__content {
        margin-top: 25px;
    }
</style>



