export default {
    // increment (state) {
    //     state.count++
    // },
    disableUser (state) {
        state.access_token = null;
        state.current_user = null;
        
    },
    updateAccessToken (state, token) {
        state.access_token = token
    },
    updateCurrUser (state, user) {
        state.current_user = user
    }
}
  


