<script>
export default {
    name: 'list-comment',
    props: {
        comments: {
            type: Array,
            required: true
        }
    },
    data() {
        return {

        }
    },
    methods: {
        
        setTimeAgo(_date){
            var vm = this;
            // var _date = new Date(_date).getTime();
            var _date = new Date(_date);
            _date = new Date(_date).getTime();

            var _time = new Date();
            _time = new Date(_time);
            
            vm.timeAgo = _time.getTime();
            var sub = (vm.timeAgo - _date)/1000;
            var re_val = "";
            if (sub >= 0 && sub < 1){
                return "Vừa xong";
            }
            else if (sub <= 60){
                var temp_str = " giây ";
                re_val = Math.floor(sub).toString() + temp_str + "trước";
            }
            else if (sub <= 3600){
                var temp_str = " phút ";
                re_val = Math.floor(sub/60).toString() + temp_str + "trước";
            }
            else if (sub <= 86400){
                var temp_str = " giờ ";
                re_val = Math.floor(sub/3600).toString() + temp_str + "trước";
            }
            else {
                var temp_str = " ngày ";
                re_val = Math.floor(sub/86400).toString() + temp_str + "trước";
            }
            return re_val;
        }
    }
}
</script>

<style scoped>
    .ass1-comments__avatar {
