<template>
    <div class="row">
        <div class="col-lg-8">
            <!--section-->
            <div class="ass1-section ass1-section__edit-post">
                <div class="ass1-section__content">
                    <form action="#">
                        <div class="form-group">
                            <label>Link ảnh từ meme</label>
                            <input v-model.trim="imageURL" type="text" class="form-control ttg-border-none" placeholder="https://">
                        </div>
                        <div class="form-group">
                            <label>Nội dung bài viết</label>
                            <textarea v-model.trim="description" rows="5" class="form-control ttg-border-none" ></textarea>
                            <!-- <input type="text" class="form-control ttg-border-none" placeholder="Mô tả ..."> -->
                        </div>
                    </form>
                    <div class="ass1-section__image">
                        <img key="image external" v-if="imageURL != ''" :src="imageURL" alt="">
                        <img key="image upload" v-else-if="fileUpload.base64 != ''" :src="fileUpload.base64" alt="">
                    </div>
                    <input class="form-control" 
                        type="file" 
                        ref="fileInputPost" 
                        style="display: none" 
                        accept="image/*"
                        @change="onFileSelected"
                    />
                    <a href="https://memeful.com/" target="_blank" class="ass1-btn ass1-btn-meme">Chế ảnh từ meme</a>
                    <a v-if="imageURL == ''" href="javascript:void(0)" v-on:click="onFileSelect" class="ass1-btn ass1-btn-meme">Đăng ảnh từ máy tính</a>
                    
                </div>
            </div>
        </div>
        <div class="col-lg-4">
            <aside class="ass1-aside ass1-aside__edit-post">
                <div>
                    <a href="#" v-on:click="(e) => handlePostEvent(e)" class="ass1-btn">Cập nhật</a>
                </div>
                <div class="ass1-aside__edit-post-head">
                    <!-- <select class="form-control">
                        <option value="">Chọn danh mục</option>
                        <option value="1">Funny</option>
                        <option value="2">Animals</option>
                        <option value="3">K-POP</option>
                        <option value="4">Anime & Manga</option>
                    </select> -->
                    
                    <label v-for="item in allCategories" :key="item.id" class="ass1-checkbox">
                        <input type="checkbox" name="category" v-model="category" :value="item.id">
                        <span></span>
                        <p>{{item.text}}</p>
                    </label>
                </div>
                <div class="ass1-aside__get-code">
                    <p>Share Link</p>
                </div>
                <div class="ass1-aside__social">
                    <a href="" class="ass1-btn-social__facebook ass1-btn-social"><i class="fa fa-facebook" aria-hidden="true"></i></a>
                    <a href="" class="ass1-btn-social__twitter ass1-btn-social"><i class="fa fa-twitter" aria-hidden="true"></i></a>
                    <a href="" class="ass1-btn-social__google ass1-btn-social"><i class="fa fa-google-plus" aria-hidden="true"></i></a>
                </div>
                <!-- <div class="ass1-aside__edit-post-footer">
                    <span style="display: block; width: 100%;">Thiết lập chế độ xem</span>
                    <label class="ass1-checkbox">
                        <input type="radio" name="state-post" checked="checked">
                        <span></span>
                        <p>Công khai</p>
                    </label>
                    <label class="ass1-checkbox">
                        <input type="radio" name="state-post">
                        <span></span>
                        <p>Riêng tư</p>
                    </label>
                    <a href="#" class="ass1-btn"><i class="icon-Delete"></i> Xóa bài đăng</a>
                </div> -->
            </aside>
        </div>
    </div>
</template>

<script>
import _ from 'lodash';
import { mapGetters } from 'vuex';
import AlertService from '@/plugins/AlertService.js';
// import * as Cookies from 'js-cookie'

export default {
    name: 'edit-post',
    data() {
        return {
            imageURL: '',
            description: '',
            category: [],
            fileUpload: {
                objImage: null,
                base64: ''
            },
            routePostId: this.$route.params.postid,
            dataPost: null
        }
    },
    watch: {
        '$route' (to, from) {
            this.routePostId = to.params.postid;
            this.fetchDataPost();
        }
    },
    created() {
        this.fetchDataPost();
    },
    computed: {
        ...mapGetters([
            'allCategories',
            'currentIDUser'
        ])
    },
    methods: {
        onFileSelect: function() {
				this.$refs.fileInputPost.click();
		},
        onFileSelected: function(event) {
            event.preventDefault();
            var vm = this;
            const files = event.target.files;
            let filename = files[0].name;
            if(filename.lastIndexOf(".") <= 0) {
                return alert("File không hợp lệ");
            }
            
            const fileReader = new FileReader();
            fileReader.addEventListener('load', function(){
                vm.fileUpload.base64 = fileReader.result;
            });
            fileReader.readAsDataURL(files[0]);
            vm.fileUpload.objImage = files[0];
        },
        resetData() {
            this.imageURL = '';
            this.description = '';
            this.category = [];
            this.fileUpload = {
                objImage: null,
                base64: ''
            },
            this.dataPost = null
        },
        checkImageURL() {
            if(!this.imageURL.match(/^(http(s?):)([/|.|\w|\s|-])*\.(?:jpg|gif|png)$/i)) {
                AlertService.warningPopup('Cảnh báo!', 'Đường dẫn hình ảnh không hợp lệ');
                return false;
            } else {
                return true;
            }
        },
        handlePostEvent: function(e) {
            e.preventDefault();
            if(this.category.length == 0) {
                AlertService.warningPopup('Cảnh báo!', 'Vui lòng chọn ít nhất một danh mục cho bài viết');
                return;
            }
            if(this.imageURL != '' && !this.checkImageURL()) {
                return;
            }
            const data = {
                url_image: this.imageURL,
                post_content: this.description,
                category: this.category,
                obj_image: this.fileUpload.objImage,
                postid: this.routePostId
            }
            this.$store.dispatch('editPost', data).then(res => {
                console.log(res);
                if(res.ok) {
                    // this.resetData();
                    AlertService.successPopup("Thành công!", res.body.message);
                } else {
                    AlertService.errorPopup("Lỗi!", res.error);
                }
            })
        },
        async fetchDataPost() {
            AlertService.loadingPopup();
            if(this.routePostId) {
                const result =  await this.$store.dispatch('getPostByPostID', this.routePostId)
                if(result.ok) {
                    // await this.fetchDataUser(result.body.data.post.USERID);
                    this.dataPost = result.body.data;
                    this.filterCategory();
                    this.setDataPost();
                    AlertService.swal.close();
                } else {
                    AlertService.swal.close();
                    AlertService.errorPopup("Lỗi!", result.error, () => {
                        this.$router.push("/");
                    });
                    // console.log(result.error.response);
                    if(result.error.response.status == 401) {
                        this.$store.dispatch('disableUser');
                    }
                }
            }
        },
        filterCategory() {
            this.category = this.dataPost.categories.map((value) => {
                return value.tag_index
            })
        },
        setDataPost() {
            this.description = this.dataPost.post.post_content;
            this.imageURL = this.dataPost.post.url_image;
        }
    }
}
</script>

<style scoped>
    .ass1-section__image {
        position: relative;
        min-height: 500px;
        background: url("/static/images/no_image_available.jpeg");
        background-size: cover;
        background-position: center;
    }
    .ass1-section__image:after {
        /* content: ''; */
        display: block;
        padding-top: 75%;
        background: url("/static/images/no_image_available.jpeg");
        background-size: cover;
        background-position: center;
    }
    .ass1-section__image img {
        width: 100%;
        height: auto;
        /* position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; */
        /* object-fit: cover; */
    }
    .ass1-btn-meme + .ass1-btn-meme {
        margin-left: 10px;
    }
    .ass1-aside .ass1-aside__edit-post-head .ass1-checkbox{
        margin-bottom: 8px;
        width: 50%;
    }
    .ass1-aside .ass1-aside__edit-post-head .ass1-checkbox p {
        margin-left: 6px;
        font-size: 14px;
    }
    .ass1-aside .ass1-aside__edit-post-head .ass1-checkbox span {
        width: 20px;
        height: 20px;
    }
</style>



